import { NavLink } from "react-router-dom";
import { IToken } from "types";
import { NULL_ADDRESS } from "config/constants";
import useCommonStyles from "style/common";
import { BigNumber } from "ethers";
import { ethers } from 'ethers';
import { ZERO } from "utils/number";
import { useSnackbar } from "notistack";
import { IDOService } from "services/ido";
import { IDOv2Service } from "services/idov2";
import { IDOSecService } from "services/idoSec";
import { IDOv3Service } from "services/idov3";
import { LegioClaimV2Service } from "services/legioClaimV2";
import { getCurrentTimeStamp } from "utils/ido";
import moment from "moment-timezone";

