interface IProps {
  id: string;
}

interface IState {
  expanded: boolean;
  personalTotalFund: BigNumber;
  personalReleased: BigNumber;
  personalReleasableAmount: BigNumber;
}

export const IDOClaimRow = (props: IProps) => {
  const { data } = useIDO(props.id); // call useService to get smart contract's data
  const classes = useStyles();
  const commonClasses = useCommonStyles();
  const [counter, setCounter] = useState(1);
  const {
    account,
    library: provider,
    networkId,
    setTxModalInfo,
    setWalletConnectModalOpened,
  } = useConnectedWeb3Context();
  const increateCounter = () => setCounter((counter) => counter + 1);
  const [state, setState] = useState<IState>({
    personalTotalFund: ZERO,
    expanded: false,
    personalReleased: ZERO,
    personalReleasableAmount: ZERO,
  });
  const isMountedRef = useIsMountedRef();
  const { enqueueSnackbar } = useSnackbar();
  const explorerUri = getEtherscanUri(networkId);

  const toggleExpand = () =>
    setState((prev) => ({ ...prev, expanded: !prev.expanded }));

  const loadPersonalInfo = async () => {
    if (!account || !provider || !data) {
      setState((prev) => ({
        ...prev,
        personalTotalFund: ZERO,
        personalReleased: ZERO,
        personalReleasableAmount: ZERO,
      }));
      return;
    }
    try {
      if (data.version === IDOVersion.v0) {
        const claimV2 = new LegioClaimV2Service(provider, account, data.address);
        const funder = await claimV2.getFunderInfo(account);
        console.log('ClaimV2 Total Amount:', ethers.utils.formatEther(funder.totalFunded), ', total Released:', ethers.utils.formatEther(funder.release))
        const claimAmountV2 = await claimV2.getClaimableAmount(account);
        console.log('V2 can claim:' + ethers.utils.formatEther(claimAmountV2))
        let vestId = await claimV2.getCurrentVestId()
        console.log('V2 claim vest Id:', ethers.utils.formatEther(vestId))

        // Show claimable value on UI
        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: claimAmountV2,
          }));
        

      } else if (data.version === IDOVersion.v1) {
        const ido = new IDOService(provider, account, data.address);
        const [funder, releasable] = await Promise.all([
          ido.getFunderInfo(account),
          ido.getClaimableAmount(account),
        ]);

        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: releasable,
          }));
      } else if (data.version === IDOVersion.v2) {
        const ido = new IDOv2Service(provider, account, data.address);
        const funder = await ido.getFunderInfo(account);
        const releasable = await ido.getClaimableAmount(account);
        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: releasable,
          }));
      } else if (data.version === IDOVersion.sec) {
        const ido = new IDOSecService(provider, account, data.address);
        const funder = await ido.getFunderInfo(account);
        const releasable = await ido.getClaimableAmount(account);

        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: releasable,
          }));
      } else {
        // v3, v4
        const ido = new IDOv3Service(provider, account, data.address);
        const funder = await ido.getFunderInfo(account);
        const releasable = await ido.getClaimableAmount(account);

        if (isMountedRef.current === true)
          setState((prev) => ({
            ...prev,
            personalTotalFund: funder.totalFunded,
            personalReleased: funder.release,
            personalReleasableAmount: releasable,
          }));
      }
    } catch (error) {
      if (isMountedRef.current === true)
        setState((prev) => ({
          ...prev,
          personalTotalFund: ZERO,
          personalReleased: ZERO,
          personalReleasableAmount: ZERO,
          tierWhitelisted: false,
        }));
    }
  };

  useEffect(() => {
    loadPersonalInfo();
  }, [account, networkId, data?.address]);

  const onClaim = async () => {
    if (!account || !provider) {
      setWalletConnectModalOpened(true);
      return;
    }
    if (!data) {
      return;
    }
    try {
      setTxModalInfo(true, "Claiming ...");
      const gasPrice = await provider.getGasPrice();

      if (data.version === IDOVersion.v0) {
        const claimV2 = new LegioClaimV2Service(provider, account, data.address);
        let estimatedGas = BigNumber.from("300000");
        try{
          const tx = await claimV2.claim(gasPrice, calculateGasMargin(estimatedGas));
          setTxModalInfo(
            true,
            "Claiming...",
            "Please wait until tx is confirmed",
            tx
          );
          await provider.waitForTransaction(tx);
        } catch (e) {
          console.log('[thomas][IDOClaimRow][onClaim] claim cancel:', e)
        }
      } else if (data.version === IDOVersion.v1) {
        const ido = new IDOService(provider, account, data.address);
        const estimatedGas = await ido.claimEstimateGas();
        const tx = await ido.claim(gasPrice, calculateGasMargin(estimatedGas));
        setTxModalInfo(
          true,
          "Claiming...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      } else if (data.version === IDOVersion.v2) {
        const ido = new IDOv2Service(provider, account, data.address);

        let estimatedGas = BigNumber.from("300000");
        const tx = await ido.claim(gasPrice, calculateGasMargin(estimatedGas));
        setTxModalInfo(
          true,
          "Claiming...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      } else if (data.version === IDOVersion.sec) {
        const ido = new IDOv3Service(provider, account, data.address);

        let estimatedGas = BigNumber.from("300000");
        try {
          estimatedGas = await ido.estimateGas("claim", []);
        } catch (error) {}
        const tx = await ido.claim(gasPrice, calculateGasMargin(estimatedGas));
        setTxModalInfo(
          true,
          "Claiming...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      } else {
        // v3, v4
        const ido = new IDOv3Service(provider, account, data.address);

        let estimatedGas = BigNumber.from("300000");
        try {
          estimatedGas = await ido.estimateGas("claim", []);
        } catch (error) {}
        const tx = await ido.claim(gasPrice, calculateGasMargin(estimatedGas));
        setTxModalInfo(
          true,
          "Claiming...",
          "Please wait until tx is confirmed",
          tx
        );
        await provider.waitForTransaction(tx);
      }
      enqueueSnackbar("Claimed successfully");
      await loadPersonalInfo();
      setTxModalInfo(false);
    } catch (error: any) {
      enqueueSnackbar(error.message || "Something went wrong", {
        variant: "error",
      });
      setTxModalInfo(false);
    }
  };

  const renderContent = () => {
    if (!data || !data.meta) return null;
    let fundToken: IToken | null = null;

    try {
      fundToken = getTokenFromAddress(data.fundToken, networkId);
    } catch (error) {
      console.log('[thomas][IDOClaimRow][renderContent] get fundToken fail')
    }

    if (!fundToken) return null;

    const nextClaimTimeStamp = (() => {
      const curTimestamp = getCurrentTimeStamp();
      if (curTimestamp < data.cliffTime) {
        return data.cliffTime;
      }
      if (curTimestamp > data.cliffTime + data.vestingDuration) {
        return 0;
      }
      return (
        data.cliffTime +
        Math.floor((curTimestamp - data.cliffTime) / data.vestingPeriodicity) *
          data.vestingPeriodicity
      );
    })();

    return (
      <>
        <div className={classes.main}>
          <div className={classes.header}>
            <div className={classes.headerLeft}>
              <LazyImage
                src={data.meta.logo}
                className={classes.avatarWrapper}
                imgClassName={classes.avatar}
              />
              <Typography className={classes.title}>
                {data.meta.title}
              </Typography>
            </div>
            <Hidden smDown>
              <div className={classes.yourAllocation}>
                <Typography className={classes.label}>Your Alloc</Typography>
                <Typography className={classes.value}>
                  {formatBigNumber(
                    state.personalTotalFund
                      .mul(data.saleTarget)
                      .div(data.fundTarget),
                    data.meta.tokenDecimals,
                    0
                  )}{" "}
                  {data.meta.tokenSymbol}
                </Typography>
              </div>
            </Hidden>
            <Hidden xsDown>
              {data.saleToken !== NULL_ADDRESS ? (
                <div className={classes.yourClaimable}>
                  <Typography className={classes.label}>
                    Your Claimable
                  </Typography>
                  <Typography className={classes.value}>
                    {data.version !== "v0" ? (
                    formatBigNumber(
                      state.personalReleasableAmount,
                      data.meta.tokenDecimals,
                      0
                    )) : (
                      formatBigNumber(
                        state.personalReleasableAmount,
                        data.meta.tokenDecimals,
                        2
                      )
                    )}{" "}
                    {data.meta.tokenSymbol}
                  </Typography>
                </div>
              ) : (
                <div className={classes.yourClaimable}>
                  <Typography className={classes.label}>Claim</Typography>
                  <Typography className={classes.value}>BulkSender</Typography>
                </div>
              )}
            </Hidden>
            <Hidden smDown>
              <div className={classes.yourClaimable}>
                <Typography className={classes.label}>Next Claim</Typography>
                <Typography className={classes.value}>
                  {nextClaimTimeStamp > 0
                    ? `${moment(new Date(nextClaimTimeStamp * 1000))
                        .utc()
                        .format("MM/DD/YYYY HH:mm")} GMT`
                    : "All unlocked"}
                </Typography>
              </div>
            </Hidden>

            {data.saleToken !== NULL_ADDRESS ? (
              <div className={classes.claimWrapper}>
                <Button
                  fullWidth
                  disabled={state.personalReleasableAmount.isZero()}
                  color="primary"
                  variant="contained"
                  className={classes.claim}
                  onClick={onClaim}
                >
                  {state.personalReleasableAmount.isZero()
                    ? "Nothing"
                    : "Claim"}
                </Button>
              </div>
            ) : (
              <div className={classes.claimWrapper}>
                <Typography className={classes.label}>-</Typography>
                <Typography className={classes.value}>-</Typography>
              </div>
            )}
            <IconButton
              disabled={data.version==="v0"}
              onClick={toggleExpand}
              className={clsx(classes.expand, state.expanded && "expanded")}
            >
              <KeyboardArrowRightIcon />
            </IconButton>
          </div>
          <div
            className={clsx(
              commonClasses.maxHeightTransition,
              state.expanded && "visible"
            )}
          >
            <div className={classes.subContent}>
              <Hidden mdUp>
                <Typography className={classes.infoRow}>
                  Your Alloc:&nbsp;
                  <span>
                    {formatBigNumber(
                      state.personalTotalFund
                        .mul(data.saleTarget)
                        .div(data.fundTarget),
                      data.meta.tokenDecimals,
                      0
                    )}{" "}
                    {data.meta.tokenSymbol}
                  </span>
                </Typography>
              </Hidden>
              <Hidden smUp>
                {data.saleToken !== NULL_ADDRESS ? (
                  <Typography className={classes.infoRow}>
                    Your Claimable:&nbsp;
                    <span>
                      {formatBigNumber(
                        state.personalReleasableAmount,
                        data.meta.tokenDecimals,
                        0
                      )}{" "}
                      {data.meta.tokenSymbol}
                    </span>
                  </Typography>
                ) : (
                  <Typography className={classes.infoRow}>
                    Claim:&nbsp;<span>BulkSender</span>
                  </Typography>
                )}
              </Hidden>

              <Hidden mdUp>
                <Typography className={classes.infoRow}>
                  Next Claim:&nbsp;
                  <span>
                    {nextClaimTimeStamp > 0
                      ? `${moment(new Date(nextClaimTimeStamp * 1000))
                          .utc()
                          .format("MM/DD/YYYY HH:mm")} GMT`
                      : "All unlocked"}
                  </span>
                </Typography>
              </Hidden>

              <a
                className={classes.viewContract}
                href={`${explorerUri}address/${data.address}`}
                target="_blank"
                rel="noreferrer"
              >
                View Sale Contract
              </a>
              <Grid container style={{ justifyContent: "center" }}>
                <Grid item xs={12} md={2}>
                  <NavLink
                    className={classes.link}
                    to={`/idos/${data.address}`}
                  >
                    Research
                    <KeyboardArrowRightIcon />
                  </NavLink>
                </Grid>
              </Grid>
            </div>
          </div>
        </div>
      </>
    );
  };

  return (
    <div className={classes.root}>
      <div className={classes.content}>
        {!data || !data.meta ? <SimpleLoader /> : renderContent()}
      </div>
    </div>
  );
};
