import { BigNumber, Contract, Wallet, ethers } from "ethers";
import { IFunder, Maybe } from "types";
import legioClaimV2Abi from "abis/legioClaimV2.json";

class LegioClaimV2Service {
    provider: any;
    contract: Contract;

    constructor(provider: any, signerAddress: Maybe<string>, addr: string) {
        console.log('[thomas][LegioClaimV2Service] constructor')
        this.provider = provider;
        if (signerAddress) {
          const signer: Wallet = provider.getSigner();
          this.contract = new ethers.Contract(addr, legioClaimV2Abi, provider).connect(
            signer
          );
        } else {
          this.contract = new ethers.Contract(addr, legioClaimV2Abi, provider);
        }
    }

    get address(): string {
        return this.contract.address;
    }
    
    async getMaxAllocation(address: string) {
        return this.contract.getMaxAllocation(address);
    }

    async getClaimableAmount(address: string) {
        return this.contract.getClaimableAmount(address);
    }

    async getFunderInfo(address: string): Promise<IFunder> {
        const funder = await this.contract.getFunderInfo(address);
        return {
          totalFunded: funder.totalFunded,
          release: funder.released,
        };
    }

    async getCurrentVestId() {
        return this.contract.getCurrentVestId();
    }

    async claim(gasPrice: BigNumber, gasLimit: BigNumber) {
        const transactionObject = await this.contract.claim({
          gasPrice,
          gasLimit,
        });
        console.log(`Claim transaction hash: ${transactionObject.hash}`);
        return transactionObject.hash;
    }
}

export { LegioClaimV2Service };