import { makeStyles, Typography } from "@material-ui/core";
import { NotAvailable, PageWrapper, SimpleLoader } from "components";
import { networkIds } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { useClaimableIDOs } from "helpers";
import { IDOClaimRow } from "./components";

const useStyles = makeStyles((theme) => ({
  root: {},
  content: {
    maxHeight: `calc(100vh - ${theme.custom.appHeaderHeight + 20}px)`,
    overflowY: "auto",
    paddingRight: 16,
  },
}));

const IDOClaimPage = () => {
  const { networkId } = useConnectedWeb3Context();
  const { data, loading, hasMore, loadMore } = useClaimableIDOs();
  const classes = useStyles();

  const handleScroll = (e: any) => {
    const bottom =
      e.target.scrollHeight - e.target.scrollTop === e.target.clientHeight;
    if (bottom) {
      if (loading || !hasMore) {
        return;
      }
      loadMore();
    }
  };

  if ([networkIds.HARMONY, networkIds.BSCTEST].includes(networkId as any)) {
    return (
      <PageWrapper>
        <NotAvailable />
      </PageWrapper>
    );
  }

  return (
    <PageWrapper>
      <Typography align="center" component="h3" variant="h1">
        IDO Claims
      </Typography>
      <div className={classes.content} onScroll={handleScroll}>
        {data.map((ido) => (
          <IDOClaimRow id={ido.address} key={ido.address} />
        ))}
        {loading && <SimpleLoader />}
      </div>
    </PageWrapper>
  );
};

export default IDOClaimPage;
